    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
}

.table {
    display: table;
    width: 100%;
    >* {
        display: table-cell;
        vertical-align: top;
    }
}

.inputfile {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.error-text {
    display: block;
    text-align: left;
    color: red;
    left: 0;
    bottom: -20px;
    pointer-events: none;
    transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    font-size: .7em;
    margin-top: 5px;
}

